/* ======================================
   VARIABLES
====================================== */
:root {
  --background: rgba(0, 0, 0, 0.973);
  --background-sticky: rgba(0, 0, 0, 0.686);
  --nav-item-color: white;
  --nav-item-color-hover: gray;
  --hero-title-color: rgba(255, 255, 255, 0.773);
  --hero-title-background: rgba(0, 0, 0, 0.9);
  --slider-btns-color: rgba(255, 255, 255, 0.7);
  --slider-btn-arrows-color: black;
  --slider-btn-arrows-color-hover: black;
}

/* ======================================
   GENERAL
====================================== */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-size: 62.5%;
  background-color: var(--background);
}

.section {
  height: 50rem;
  width: 100%;
}

.section--hidden {
  opacity: 0;
  transform: translateY(5rem);
}

/* ======================================
   NAVIGATION
====================================== */
.nav {
  top: 0;
  z-index: 10;
  background-color: var(--background-sticky);
  height: 5rem;
  width: 100%;
}

.sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.nav-container {
  max-width: 100%;
  margin: 0 auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.nav__logo {
  height: 4.3rem;
  overflow: hidden;
}

.nav__links {
  display: flex;
  list-style: none;
  gap: 2.2rem;
}

.nav__link:link,
.nav__link:visited {
  font-size: 1.4rem;
  font-weight: 500;
  color: var(--nav-item-color);
  text-decoration: none;
}

.nav__link:hover {
  color: var(--nav-item-color-hover);
}

/* ======================================
   HERO SECTION
====================================== */
.hero {
  position: relative;
  overflow: hidden;
}

.hero img {
  opacity: 0.6;
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.content-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  opacity: 0.5;
  color: var(--hero-title-color);
  background-color: var(--hero-title-background);
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  text-align: center;
  z-index: 10;
}

/* ======================================
   SLIDER SECTION
====================================== */
.slide-section {
  position: relative;
  margin-top: 10rem;
  height: 50rem;
}

.slides-container {
  height: 100%;
  width: 100%;
  position: relative;
}

.slider {
  max-width: 70%;
  height: 40rem;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.slider-controls {
  position: absolute;
  top: 1rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 1rem;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.5rem 1rem;
  border-radius: 8rem;
  backdrop-filter: blur(4px);
}

.slide {
  position: absolute;
  top: 0;
  width: 100%;
  height: 50rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 1s;
  backface-visibility: hidden;
}

.slide > img {
  max-width: 1100px;
  max-height: 700px;
  opacity: 0.95;
  width: 100%;
  height: 100%;
  object-fit: contain;
  pointer-events: none;
}

.slider__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  background-color: var(--slider-btns-color);
  font-family: inherit;
  color: var(--slider-btn-arrows-color);
  border-radius: 50% 20%;
  height: 4rem;
  width: 4rem;
  font-size: 3.25rem;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.slider__btn--left {
  left: 4%;
  transform: translate(-50%, -50%);
}

.slider__btn--left:active {
  opacity: 0.7;
  color: var(--slider-btn-arrows-color-hover);
}

.slider__btn--right {
  right: 4%;
  transform: translate(50%, -50%);
}

.slider__btn--right:active {
  opacity: 0.7;
  color: var(--slider-btn-arrows-color-hover);
}

.btn-image-slide {
  padding: 1rem 1.6rem;
  font-size: 1.3rem;
  background-color: var(--background);
  color: var(--slider-btns-color);
  border: 1px solid white;
  border-radius: 1rem;
  cursor: pointer;
}

.btn-image-slide:hover {
  background-color: rgba(152, 152, 152, 0.801);
  color: white;
}
.btn-image-slide:active {
  transform: translateY(0.3rem);
}
/* ======================================
   ACERCA DE MI SECTION
====================================== */
.acerca-de-container {
  margin-top: 10rem;
  padding: 1rem;
  display: flex;
  gap: 0;
}

.acerca-de-img {
  width: 50%;
  padding: 5rem;
}
.acerca-de-img:hover {
  transform: scale(0.9);
}

.acerca-de-content {
  padding: 10rem;
  width: 50%;
}

.acerca-de-content p {
  line-height: 1.3;
  font-size: 1.1rem;
  color: white;
  text-align: justify;
}
.acerca-de-title {
  line-height: 2;
  color: white;
  text-align: center;
  font-size: 2rem;
}
.acerca-de-title:hover {
  color: gray;
}
.acerca-de-img img {
  opacity: 0.95;
  max-width: 100%;
}

.img-name {
  color: white;
  font-size: 1rem;
}

/* ======================================
   EVENTOS SECTION
====================================== */
.hidden {
  display: none;
  opacity: 0;
}

.slide-event-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  gap: 1rem;
}

.event-slider,
.event-text-container {
  width: 50%;
  box-sizing: border-box;
}

.event-slider {
  position: relative;
  overflow: hidden;
  height: 50rem;
}

.slide-event {
  position: absolute;
  top: 0;
  width: 100%;
  height: 50rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 1s;
  backface-visibility: hidden;
}

.slide-event img {
  max-width: 1100px;
  max-height: 700px;
  border-radius: 8rem;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.event-text-container {
  margin: 0 auto;
  padding: 3rem;
}

.event-text {
  font-size: 1.2rem;
  line-height: 1.5;
  color: white;
  padding: 1.5rem;
  text-align: justify;
}

.event-title {
  padding: 1.2rem;
  font-size: 2.3rem;
  line-height: 2.3;
  color: white;
  text-align: center;
}

.event-content {
  color: white;
  list-style-type: square;
  list-style-position: inside;
  text-align: justify;
  font-size: 1.5rem;
}
